import React from 'react';
import { NavLink } from 'react-router-dom';
import { Card } from 'react-bootstrap';


const NavSquare = ({tab, cardClick, drawerAction}) => {

    return (
        <>
            <Card onClick={cardClick} onKeyDown={cardClick}> 
                <Card.Img variant="top" src={require(/* webpackMode: "eager" */ `../icons/${tab.image}`)} />
                <Card.Body>
                    <Card.Title>{tab.title}</Card.Title>
                    <Card.Text>
                        {tab.description}
                    </Card.Text>
                </Card.Body>
                <Card.Footer>
                    <div className="c-drawer-navlink-parent">
                        <NavLink className={tab.cls} to={tab.to} activeStyle={{fontWeight: '900'}}
                            onClick={() => drawerAction(false)} onKeyDown={() => drawerAction(false)} exact>
                            {tab.name}
                        </NavLink>
                    </div>
                </Card.Footer>
            </Card>
        </>
    );
};

export default NavSquare;

