import React, {useState} from 'react';
import { Button, Toast } from 'react-bootstrap';
import ReCAPTCHA from "react-google-recaptcha";
import './ContactForm.css';

const ContactForm = () => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');
    const [showToast, setShowToast] = useState(false);
    const [toastMessage, setToastMessage] = useState('');
    const [submitDisabled, setSubmitDisabled] = useState('disabled');

    const sendEnquiry = async () => {
        const result = await fetch(`/api/enquiry`, {
            method: 'post',
            body: JSON.stringify({name, email, message}),
            headers: {
                'Content-Type': 'application/json',
            }
        });

        const body = await result.json();

        if (body.status === 'success') {
            setToastMessage('Thanks for contacting us, we will get back to you shortly.')
            setShowToast(true);
        } else {
            setToastMessage('Please try again later as there is some issue reaching us.')
            setShowToast(true);
        }
        setName('');
        setEmail('');
        setMessage('');
    };

    const toggleToast = () => setShowToast(!showToast);

    return (
        <div className="c-contact-form">
            <form method="POST">
                <div className="form-group">
                    <label>Name</label>
                    <input type="text" className="form-control" value={name} onChange={(event) => setName(event.target.value)} />
                </div>
                <div className="form-group">
                    <label>Email address</label>
                    <input type="email" className="form-control" value={email} onChange={(event) => setEmail(event.target.value)} />
                </div>
                <div className="form-group">
                    <label>Message</label>
                    <textarea className="form-control" rows="5" value={message} onChange={(event) => setMessage(event.target.value)} />
                </div>
                <ReCAPTCHA
                    sitekey="6LcEQ6cZAAAAAIrqrCXZ1pffB6Fs8rwTwnCvk939"
                    onChange={() => setSubmitDisabled('')}
                />
                <div className="form-group c-contact-form-captcha-parent"> 
                    <Button onClick={() => sendEnquiry()} disabled={submitDisabled}>Submit</Button>
                </div>
                <Toast show={showToast} onClose={toggleToast} delay={5000} autohide  style={{position: 'absolute', top: 0, right: 0,}}>
                    <Toast.Header>
                        <strong className="mr-auto">Notification</strong>
                    </Toast.Header>
                    <Toast.Body>{toastMessage}</Toast.Body>
                </Toast>
            </form>
        </div>
    );
};

export default ContactForm;