import React from 'react';
import './HomePage.css';
import { NavLink } from 'react-router-dom';


const HomePage = () => {

    return (
        <React.Fragment>
            <div className="c-home-section-top">
                <div className="c-home-section">
                    <div className="c-home-section-img">
                    </div>
                    <div className="c-home-section-bottom">
                        <div className="c-home-section-text">
                            <div>
                                <p className="c-home-section-p">
                                    We do our part for every 100 transactions!!! <br/>
                                </p>
                            </div>
                            <div className="c-home-section-bottom-btn">
                                <NavLink className="btn btn-primary" to="/products-list">Discover Products</NavLink>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default HomePage;