import React from 'react';
import about from './../icons/about.jpg';
import contactus from './../icons/contactus.jpg';
import ContactForm from '../components/ContactForm';
import './AboutPage.css';

const AboutPage = () => (
    <React.Fragment>
        <div className="c-about-page-top">
            <div className="c-about-section">
                <div className="c-about-section-img">
                    <img alt="about us" className="c-about-section-img-img" src={about}/>
                </div>
                <div className="c-about-section-bottom">
                    <div className="c-about-section-text">
                        <div>
                            <h3 className="c-about-section-h3">Behon3st Group</h3>
                            <p className="c-about-section-p">Behon3st group is an Indian company producing export quality food items.
                                We are a group of like minded and dedicated individuals, trying to make a change in all aspects of food related business.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
            <div className="c-contact-section">
                <div className="c-contact-section-img">
                    <img alt="contact us" className="c-contact-section-img-img" src={contactus}/>
                </div>
                <div className="c-contact-section-bottom">
                    <div className="c-contact-section-text">
                        <h3 className="c-contact-section-h3">Contact Us</h3>
                        <ContactForm />
                    </div>
                </div>
            </div>
        </div>
    </React.Fragment>
)

export default AboutPage;