import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Color from 'color';
import TextField from '@material-ui/core/TextField';



const useStyles = makeStyles((theme) => ({
    actionArea: {
      borderRadius: 16,
      transition: '0.2s',
      '&:hover': {
        transform: 'scale(1.1)',
      },
    },
    card: ({ color }) => ({
      maxWidth: 400,
      minWidth: 400,
      //minHeight: 600,
      maxHeight: 600,
      [theme.breakpoints.up('xs')]: {
          maxWidth: 340,
          minWidth: 340,
          //minHeight: 600,
          maxHeight: 600,
      },
      borderRadius: 16,
      boxShadow: 'none',
      '&:hover': {
        boxShadow: `0 6px 12px 0 ${Color(color)
          .rotate(-12)
          .darken(0.2)
          .fade(0.5)}`,
      },
    }),
    content: ({ color }) => {
      return {
        backgroundColor: color,
        padding: '1rem 1.5rem 1.5rem',
      };
    },
    media: {
    },
    title: {
      fontFamily: 'Keania One',
      fontSize: '2rem',
      color: '#fff',
      textTransform: 'uppercase',
    },
    subtitle: {
      fontFamily: 'Montserrat',
      color: '#fff',
      opacity: 0.87,
      marginTop: '2rem',
      fontWeight: 500,
      fontSize: 14,
    },
}));

export default function Product({productImage, title, description, action}) {
    const classes = useStyles({ color: '#203f52' });
    const [disableBuyButton, setDisableBuyButton] = useState(false);
    const [quantityValid, setQuantityValid] = useState(true);
    const [quantity, setQuantity] = useState(1);

    const sendOrder = async (evt) => {
        setDisableBuyButton(true);

        const result = await fetch(`/api/pay`, {
            method: 'post',
            body: JSON.stringify({unitprice: 50000, quantity: quantity, receiptId: 'testtransaction:honey'}),
            headers: {
                'Content-Type': 'application/json',
            }
        });

        const order = await result.json();

        if (order.status !== 'error') {
            setDisableBuyButton(false);
        } else {
        }

        var options = {
            "key": "rzp_test_H3pXxqu1efAaPC", // Enter the Key ID generated from the Dashboard
            "amount": "50000", // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
            "currency": "INR",
            "name": "Behon3st Group",
            "description": "Test Transaction",
            //"image": "https://example.com/your_logo",
            "order_id": order.id, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
            handler: function (response) {
                alert(response.razorpay_payment_id);
                alert(response.razorpay_order_id);
                alert(response.razorpay_signature)
            },
            prefill: {
                "name": "Varun Balakrishnan",
                "email": "cbvarun@yahoo.com",
                "contact": "9663371728"
            },
            "notes": {
                "address": "Behon3st"
            },
            "theme": {
                "color": "#F37254"
            }
        };
        var rzp1 = new window.Razorpay(options);        
        rzp1.open();
        //evt.preventDefault();
    }

    function updateQuantity (evt) {
        console.log('quantity updated');
        if (evt.target.value > 0) {
            setQuantity(evt.target.value);
            setDisableBuyButton(false);
            setQuantityValid(evt.target.value > 0);
        } else {
            setQuantityValid(false);
            setDisableBuyButton(true);
        }
    }

    return (
        <Grid item>
            <Card className={classes.card}>
                <CardActionArea className={classes.actionArea}>
                    <CardMedia className={classes.media}
                        component="img"
                        alt={title}
                        image={require(/* webpackMode: "eager" */ `../icons/${productImage}`)}
                        title={title}
                    />
                    <CardContent className={classes.content}>
                        <Typography variant="h2" className={classes.title}>
                            {title}
                        </Typography>
                        <Typography className={classes.subtitle} component="p">
                            {description}
                        </Typography>
                    </CardContent>
                </CardActionArea>
                <CardActions>
                    <TextField
                        required
                        error={!quantityValid}
                        size="small"
                        id="outlined-number"
                        label="Quantity"
                        type="number"
                        defaultValue="1"
                        InputLabelProps={{
                            shrink: true,
                        }}
                        variant="outlined"
                        onChange={updateQuantity}
                    />
                    <Button disabled={disableBuyButton} size="small" color="primary" onClick={(evt) => sendOrder(evt)}>
                        {action}
                    </Button>
                </CardActions>
            </Card>
      </Grid>              
  );
}