import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';
import MenuIcon from '@material-ui/icons/Menu';
import {CardDeck, Modal} from 'react-bootstrap'
import NavSquare from './NavSquare';
import './NavigationBar.css';

const NavigationBar = ({company, navCls, navRole, tabs}) => {

  const [drawerOpen, setDrawerOpen] = useState(false);
  const location = useLocation();
  let tabsToDisplay = tabs.filter(item => item.to !== location.pathname);

  function handleClick (event) {
      setDrawerOpen(false)        
      event.currentTarget.querySelector('.c-nav-item').click();
  }

  return (
    <>
      {company}
      <div className={navCls}>
        <MenuIcon onClick={() => setDrawerOpen(true)} style={{fontSize: 50, cursor: "pointer", background: "white"}}></MenuIcon>
      </div>
      {drawerOpen ? 
        <div> 
            <Modal centered show={drawerOpen} onHide={() => setDrawerOpen(false)} dialogClassName="c-drawer-modal-size">
                <Modal.Body>
                    <CardDeck>
                        {
                            tabsToDisplay.map((tab, key) => (
                                <NavSquare key={key} tab={tab} cardClick={handleClick} drawerAction={setDrawerOpen}/>
                            ))
                        }
                    </CardDeck>
                </Modal.Body>
            </Modal>
        </div>
        : ''}
    </>
  );
};

export default NavigationBar;