import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch
} from 'react-router-dom';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ProductsPage from './pages/ProductsPage';
import NavigationBar from './navigation/NavigationBar';
import NotFoundPage from './pages/NotFoundPage';
import CompanyTitleWithLogo from './navigation/CompanyTitleWithLogo';
import clogo from './icons/CLogo.png';
import './App.css';
import tabs from './tabs.js';
import Footer from './navigation/Footer';


function App() {
  return (
    <Router>
        <div className="c-tb-content">
          <NavigationBar company={<CompanyTitleWithLogo imgCls="c-company-logo" img={clogo}/>}
            navCls="c-main-navigation"
            tabs={tabs}/>
        </div>
        <Switch>
          <Route path="/" component={HomePage} exact/>
          <Route path="/about" component={AboutPage}/>
          <Route path="/products-list" component={ProductsPage}/>
          <Route component={NotFoundPage}/>
        </Switch>
        <Footer />
    </Router>
  );
}

export default App;
